

































import { Component, Vue, Prop, Emit } from "vue-property-decorator";
import { Calendar, Table, Week } from "@/components/calendar/Calendar";

@Component
export default class CalendarBase extends Vue {
  @Prop({ type: Calendar, required: true })
  calendar!: Calendar;

  @Emit("click")
  onClick(date: Date) {}

  get title(): string {
    return this.calendar.title;
  }

  get weeks(): Week[] {
    return this.calendar.weekTexts;
  }

  get table(): Table {
    return Calendar.buildCalendarTable(
      this.calendar.year,
      this.calendar.monthIndex,
      this.calendar.month
    );
  }
}
